import React, { useState } from 'react';
import './addappointment.css' 
import { useForm } from 'react-hook-form';
import axios from 'axios';


const AddAppointment = () => {
  const { register, handleSubmit } = useForm();
  const[appointment,setAppointment]=useState('null');

  const Submit = (data) => {

    axios.post("http://localhost:4000/appointment/add",data)
      .then((res) => {
        console.log(res.data.data);
        setAppointment(res.data.data);
      })
      .catch((err) => {
        console.log("Error in adding appointments:", err);
      });
  };

  return (
<div className="add-appointment-container">
  <div className="add-appointment-first-div">   
  <h1 className="add-appointment-title">ADD APPOINTMENT</h1>
    <form onSubmit={handleSubmit(Submit)} className="add-appointment-form">
      <div>
        <label className="add-appointment-label">Name:</label>
        <input
          type="text"
          id="name"
          {...register("name", { required: true })}
          placeholder="Enter your name"
          className="add-appointment-input"
        />
      </div>

      <div>
        <label className="add-appointment-label">Email:</label>
        <input
          type="email"
          id="email"
          {...register("email", { required: true })}
          placeholder="Enter your email"
          className="add-appointment-input"
        />
      </div>

      <div>
        <label className="add-appointment-label">Date:</label>
        <input
          type="date"
          id="date"
          {...register("date", { required: true })}
          placeholder="Enter the date"
          className="add-appointment-input"
        />
      </div>

      <div>
        <label className="add-appointment-label">Time:</label>
        <input
          type="time"
          id="time"
          {...register("time", { required: true })}
          placeholder="Enter the time"
          className="add-appointment-input"
        />
      </div>

      <button type="submit" className="add-appointment-submit-button">Submit</button>
    </form>
  </div>
  </div>
  );
};

export default AddAppointment;
