//register
import React, { useState ,useEffect} from 'react';
import './register.css'
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Register = () => {

const {register,handleSubmit} = useForm();
var navigate = useNavigate();
const [roles,setroles]= useState()

const Submit = (data) =>{

    axios.post("http://localhost:4000/user/register",data).then((res)=>{
        console.log(res.data)
        navigate("/login")
    }).catch((err)=>{
        console.log(err,err.message)
    })
}

useEffect(() => {
    getRoles();
}, [])

const getRoles = () =>{
    axios.get("http://localhost:4000/role/list").then((res)=>{
        setroles(res.data.data)
    })
}


    return (
        <>
    <div className='register-main'>
      <div className='register-box'>
        <h2 className='register-title'>Registration</h2>
        <form onSubmit={handleSubmit(Submit)}>
          <label className='register-label'>Name</label>
          <input type='text' placeholder='Enter your name' className='register-input' {...register("name")} /><br />
          <label className='register-label'>Email</label>
          <input type='email' placeholder='Enter your email' className='register-input' {...register("email")} /> <br />
          <label className='register-label'>Password</label>
          <input type='password' placeholder='enter your password' className='register-input' {...register("password")} /><br />
          <label className='register-label'>Roles</label>
          <select id="roles" className='register-input' {...register("role")}>
  <option value="">Select a role</option>
  {roles?.map((role) => (
    <option key={role._id} value={role._id}>
      {role.name}
    </option>
  ))}
</select>
          <button type='submit' className='register-button'>Submit</button>
        </form>
      </div>
    </div>
  </>
);
    
}

export default Register