import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './token.css'

const Token = () => {
  const { register, handleSubmit } = useForm();
  const [token, setToken] = useState('');
  const [user, setUser] = useState({}); 
  const [errorMessage, setErrorMessage] = useState(''); 
  const navigate = useNavigate();


  const Submit = (data) => {
    axios
      .post('http://localhost:4000/token/add', data)
      .then((res) => {
        console.log('Token Number:', res.data.tokenNumber);
        setToken(res.data.tokenNumber);
        console.log('User Data:', res.data.user);
        setUser(res.data.user);
        setErrorMessage('');
        navigate(`/token-display/${res.data.tokenNumber}`);

      })
      .catch((err) => {
        if (err.response && err.response.status === 400) {
          setErrorMessage('Daily token limit exceeded. Try again tomorrow.');
        } else {
          console.log('Error in adding token:', err);
          setErrorMessage('An error occurred while generating the token.');
        }
      });
  };

  return (
    <div className="token-container">
    <div className="token-first-div"> 
       <h1 class="token-title-title">ADD TOKEN</h1>
    <form onSubmit={handleSubmit(Submit)}>
      <div>
        <label>Name:</label>
        <input
                    className="token-input"
          type="text"
          id="name"
          {...register("name")} 
          placeholder="Enter your name"
        />
      </div>

      <div>
        <label>Contact:</label>
        <input className="token-input"
          type="tel"
          id="contact"
          {...register("contact")} 
          placeholder="Enter your contact"
        />
      </div>

      <div>
        <label>Age:</label>
        <input className="token-input"
          type="number"
          id="age"
          {...register("age")} 
          placeholder="Enter the age"
        />
      </div>

      <div>
        <label>Gender:</label>
        <select id="gender" {...register('gender')}>
            <option value="male">Male</option>
            <option value="female">Female</option>
          </select>
      </div>

     

      <button className="token-submit-button" type="submit">Submit</button>
    </form>
    {errorMessage && <h6 className="error-message">{errorMessage}</h6>}
</div>
    </div>
  );
};

export default Token;
