import React from 'react';
import './login.css';
import { useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import axios from 'axios';


const Login = () => {
var navigate = useNavigate()

const {register,handleSubmit}=useForm();
const submit = (data)=>{

    axios.post('http://localhost:4000/user/user/login',data).then((res)=>{
        if(res.data.user){
            console.log("user found....")
            //console.log(res.data.user)
            console.log(res.data.user?._id)
            localStorage.setItem("_id",res.data.user?._id)
            console.log(res.data.user?.role.name)
            if(res.data.user?.role.name ==="doctor"){

              navigate("/doctordashboard")
          }
          else if(res.data.user?.role.name==="patient"){
            navigate("/patientdashboard")
          }
          else if(res.data.user?.role.name==="compounder"){
            navigate("/compounderdashboard")
          }
          



        }
    }).catch((err)=>{
        console.log("user not found....")
        alert("user not found....")
    })
}

    return (
        <div className="login-main">
        <div className="login-box">
          <h2 className="login-h2">Login</h2>
          <form onSubmit={handleSubmit(submit)}>
            <label className="login-label">Email</label>
            <input
              type="email"
              placeholder="Enter your email"
              id="email"
              autoComplete="off"
              name=""
              {...register("email")}
              className="login-input"
            />
            <br />
            <label className="login-label">Password</label>
            <input
              type="password"
              placeholder="enter your password"
              id="password"
              {...register("password")}
              className="login-input"
            />
            <br />
            <button type="submit" className="login-button">Submit</button>
          </form>
        </div>
      </div>
      
    )
}

export default Login